// ********************　PC(と大きいタブレット)とSP(と小さいタブレット)を分ける場合(ブレイクポイントは1つ) ********************
@mixin sp {
  @media screen and (max-width: 768px) {
    @content;
  }
}

@mixin tablet {
  @media screen and (min-width: 769px) and (max-width: 1024px) {
    @content;
  }
}

@mixin pc {
  @media screen and (min-width: 769px) {
    @content;
  }
}
